.slider__btn--disabled{background-color:#E5E5E5}.slider__pag--active{position:relative;background-color:#F4A300}.slider__pag--active::after{content:'';top:50%;transform:translate(-50%, -50%);left:50%;position:absolute;display:block;width:1.75rem;height:1.75rem;background-color:transparent;border-radius:50%;border:0.125rem solid #F4A300}.menu__pag--active{position:relative;background-color:#FFFFFF}.menu__pag--active::after{content:'';top:50%;transform:translate(-50%, -50%);left:50%;position:absolute;display:block;width:1.75rem;height:1.75rem;background-color:transparent;border-radius:50%;border:0.125rem solid #FFFFFF}.cards__slide--active{transform:scale(1.4);margin-left:3rem;margin-right:8rem}.insta__wrap{min-height:inherit;transition-timing-function:linear}@media (max-width: 1400px) and (min-width: 991px){.cards__slide--active{transform:scale(1.3);margin-left:2rem;margin-right:7rem}}@media (max-width: 991px) and (min-width: 768px){.cards__slide--active{transform:scale(1.2);margin-left:2rem;margin-right:5rem}}@media (max-width: 768px){.cards__slide--active{transform:scale(1.2);margin-left:1rem;margin-right:3rem}}
